.overlay-loader-cnt {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    z-index: 20;
    background: rgba(68, 67, 67, 0.5);
  }

  .loader-cnt {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
  }
  
  @keyframes loader {
    0% {
      opacity: 0.2;
    }
    50% {
      opacity: 1;
      transform: scale(1.2);
    }
    100% {
      opacity: 0;
      transform: scale(0);
    }
  }
  .shoeLogo {
    position: relative;
    width: 130px;
    height: 150px;
    margin-top: -100px;
    border: 5px solid #fff;
    background-color: #000;
    clip-path: polygon(
      66% 80%,
      45% 68%,
      37% 75%,
      23% 77%,
      15% 70%,
      9% 82%,
      10% 97%,
      11% 100%,
      48% 100%,
      78% 100%,
      89% 99%,
      91% 94%,
      87% 86%,
      78% 83%
    );
    animation: animateshoe 1s infinite;
    transition: all 0.3s;
  }
  .shoe1 {
    // animation-delay: .2s;
  }
  .shoe2 {
    animation-delay: .5s;
  }
  
  @keyframes animateshoe {
    0% {
      transform: translateY(0px);
    }
  
    10% {
      transform: translateY(-10px);
    }
    20% {
      transform: translateY(-20px);
    }
    25% {
      transform: translatex(10px);
    }
    45% {
      transform: rotate(15deg);
    }
    50% {
      transform: translatex(0px);
    }
  
    100% {
      transform: rotate(0deg);
    }
  }